body{background:#e7f2ea}.LunchBot{margin:0 auto;max-width:650px;padding:50px}.LunchBot h1.pageTitle{color:#c2577b;font-size:40px}.LunchBot hr{margin:50px 0}.LunchBot h3{font-size:24px;font-weight:300}.LunchBot .placesList{list-style:none;margin:30px 0;padding:0}.LunchBot .placesList>li{background:#fff;border:1px solid rgba(0,0,0,.1);border-bottom:none;font-size:26px;padding:20px}.LunchBot .placesList>li:before{color:#888;display:block;font-size:12px;font-weight:300;padding-bottom:2px;text-transform:uppercase}.LunchBot .placesList>li:first-child{background:#82708e;color:#fff;font-weight:500}.LunchBot .placesList>li:first-child:before{color:#ccc;content:"My top suggestion"}.LunchBot .placesList>li:nth-child(2):before{content:"Okay. What about"}.LunchBot .placesList>li:nth-child(3):before{content:"Or maybe even"}.LunchBot .placesList>li:first-child{border-top-left-radius:5px;border-top-right-radius:5px}.LunchBot .placesList>li:last-child{border-bottom:1px solid rgba(0,0,0,.1);border-bottom-left-radius:5px;border-bottom-right-radius:5px}.LunchBot footer{color:#888;font-size:14px}.LunchBot footer a{color:#60d5da}